.sideMenuWrapper {
    position: sticky;
    right: 0%;
    left: 0;
    top: 0%;
    width: 15rem;
    padding-top: 6%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: hsl(0, 0%, 0%, 0.1);
    border-right: 1px solid hsl(0, 0%, 0%, 0.2);
    box-shadow: 0px 2px 5px hsl(0, 0%, 0%, 0.4);
}

.Header {
    width: 65%;
    text-align: center;
}

.Header Img {
    max-width: 100%;
    border-radius: 100vw;
    margin-bottom: 1rem;
    border: 2px solid rebeccapurple;
}

.Actions {
    width: 100%;
    margin-top: auto;
    margin-bottom: auto;
}

.Actions ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.Actions ul li {
    margin-left: auto;
    margin-right: auto;
    display: grid;
    place-items: center;
    width: 80%;
    height: 2.8rem;
    background-color: #fff;
    border-radius: 0.5rem;
    cursor: pointer;
    overflow: hidden;
}

.Actions ul li:last-child {
    height: 2.9rem;
    background-color: green;
    color: #fff;
}

.Logoutinput {
    height: 2.9rem;
    width: 100%;
    background: none;
    border: none;
    cursor: pointer;
    color: #fff;
}

.Link {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    padding-left: 10%;
    justify-content: flex-start;
    align-items: center;
    gap: 5%;
}

.Link.active {
    transition: background 200ms cubic-bezier(0, 0.99, 0.6, 1.12);
    background-color: #353535;
    color: #fff;
}

@media screen and (max-width: 720px) {
    .sideMenuWrapper {
        position: fixed;
        bottom: 0%;
        top: unset;
        width: 100vw;
        height: 10vh;
        flex-direction: row;
        padding-top: unset;
        justify-content: flex-start;
    }
    .Header h4 {
        display: none;
    }
    .Header Img {
        width: 3rem;
        margin-bottom: 0%;
    }
    .Actions ul {
        padding: 0 1rem;
        width: 100%;
        height: 10vh;
        align-items: center;
        flex-direction: row;
        gap: 1rem;
    }
    .Actions ul li {
        flex-grow: 1;
        justify-content: center;
    }
    .Actions ul li p {
        display: none;
    }
    .Actions ul li:last-child {
        height: 2.2rem;
    }
}